  /************************************************/
  /*************** GENERAL SETTINGS ***************/
  /************************************************/
  
  *,
  :before,
  :after {
    box-sizing: border-box;
  }
  
  body {
    background-color: #ffff;
    color: #0230e6;
    font: 16px/1.25 'Raleway', sans-serif;
    padding: 0.5em;
    
  }
  
  main {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border: 1px solid red;
    border-radius: 25px;
    margin-top: 100px;
    
    
  }
  footer {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 64em;
    width: 100%;
    height: 250px;
    border: 1px solid red;
    border-radius: 25px;
  }
  
  /* Headings */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    margin: 0.25em 0;
    text-align: center;
  }
  
  /* Links */
  a {
    color: #a2ed56;
  }
  
  a:hover {
    color: #83e4e2;
  }
  
  
  
  /************************************************/
  /********************* TABLE ********************/
  /************************************************/
  
  table {
    border-collapse: collapse;
    border-spacing: 0;
    max-width: 100%;
    width: 100%;
  }
  
  
  
  /*** HEADER ***/
  table thead {
    border-bottom: 2px solid #e5e5e5;
  }
  
  table th,
  table td {
    padding: 1em 0.5em;
    position: relative;
  }
  
  table th {
    font: 400 1.25em/1 'Oswald', sans-serif;
    letter-spacing: 0.025em;
    outline: none;
    padding-right: 2em;
    text-align: center;
    text-transform: uppercase;
    transition: color 0.2s ease-in-out;
  }
  
  table th:hover,
  table th:focus {
    color: #050505;
  }
  
  /* Arrows */
  table th:after,
  table th:before {
    border: 0.35em solid transparent;
    content: '';
    height: 0; 
    position: absolute;
    right: 0.65em;
    top: 50%;
    transform: translateY(-50%);
    transition: border-color 0.2s ease-in-out;
    width: 0; 
  }
  
  table th:before {
    border-bottom-color: rgba(255, 255, 255, 0.25);
    border-top: 0;
    margin-top: -0.35em;
  }
  
  table th:after {
    border-bottom: 0;
    border-top-color: rgba(255, 255, 255, 0.25);
    margin-top: 0.35em;
  }
  
  table th:hover:before,
  table th:focus:before {
    border-bottom-color: rgba(255, 255, 255, 0.75);
  }
  
  table th:hover:after,
  table th:focus:after {
    border-top-color: rgba(255, 255, 255, 0.75);
  }
  
  table th[aria-sort="ascending"],
  table th[aria-sort="descending"] {
    color: #050505;
  }
  
  table th[aria-sort="ascending"]:before {
    border-bottom-color: #f9fcf7;
    opacity: 0.1;
  }
  
  table th[aria-sort="ascending"]:after {
    border-top-color: rgba(255, 255, 255, 0.25);
  }
  
  table th[aria-sort="descending"]:before {
    border-bottom-color: rgba(255, 255, 255, 0.25);
  }
  
  table th[aria-sort="descending"]:after {
    border-top-color: #f9fcf7;
    opacity: 0.1;
  }
  
