  /* Name Cell */
  table td[data-label="Vozac"] {
    font-weight: 500;
    letter-spacing: 0.01em;
    text-align: center;
  }
  
  /* Animal Cells */
  table td[data-label="Br-utrka"],
  table td[data-label="Br-pob"],
  table td[data-label="Br-pod"],
  table td[data-label="Br-bod"] {
    text-align: center;
  }
  
  /* Check and Cross Marks */
  table tr span.yes,
  table tr span.no {
    transition: all 0.2s ease-in-out;
  }
  
  table tr:hover span.yes,
  table tr:hover span.no {
    color: #fff;
  }
  
  table span.yes {
    color: #a2ed56;
    font-size: 1.25em;
  }
  
  table span.no {
    color: #fd6470;
    font-size: 0.875em;
  }
  
  
  
  /*** FOOTER ***/
  footer {
    text-align: center;
  }
  
  footer a {
    display: inline-block;
  }
  
  
  
  
  
  /************************************************/
  /***************** MEDIA QUERIES ****************/
  /************************************************/
  @media (max-width: 960px) {
  
    /*** TABLE ***/
    table tr,
    table td {
      display: block;
    }
  
    table tr {
      padding: 0.75em 0;
    }
  
    /* Header */
    table thead tr {
      text-align: center;
    }
  
    table th {
      display: block;
      padding: 0.25em 0.5em;
      text-align: center;
    }
  
    /* Body */
    table td {
      padding: 0.25em 0.5em 0.25em 6.5em;
      
    }
  
    table td:before {
      content: attr(data-label);
      font: 400 1.125em/1 'Oswald', sans-serif;
      left: 0.5em;
      position: absolute;
      top: 0.125em;
      width: 100%;
      text-align: left;
    }
  
    /* Animal Cells */
  table td[data-label="Br-utrka"],
  table td[data-label="Br-pob"],
  table td[data-label="Br-pod"],
  table td[data-label="Team"],
  
  table td[data-label="Br-bod"] {
      text-align: center;
    }
  
    /* Check and Cross Marks */
    table span.yes {
      line-height: 0.875;
